* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
.card {
    background: rgb(248, 246, 246);
    border-radius: 2px;
    height: 250px;
    margin: 1rem;
    margin-top: 10px;
    position: relative;
    width: 280px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:rgb(243, 242, 242);
  }
  .card > .img-container > img {
    width: 100%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }

  .hover img:hover
.wrapper {
    padding-top: 10px;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    overflow: auto;
    margin-left: auto;
    margin-right: auto;
  
  }
.header {
    width: 100%;
    font-size: 2rem;
    color: rgb(8, 8, 8);
    margin-bottom: 10px;
  }
  
  .title {
    float: left;
  }
  
  .scores {
    float: left;
  }
.App-header {
  background-color: #222;
  height: 80px;
  padding: 10px;
  color: white;
}

.App-title {
  font-size: 2em;
}

.App-intro {
  font-size: large;
}
